import React from 'react';
import s from'./users.module.css';
import userPhoto from '../../assets/img/user.png'
import { NavLink } from 'react-router-dom';


const User = (props) => {
    const { user, unfollow, follow, followingInProgress} = props
    const {id, photos, name, status, followed} = user
    const userId = id
    return (  
      <div  className={`${s.userItem}`}>
          <div className='container-fluid'>
              <div className='row'>
                  <div className='col-sm-3'>
                      <div className=''>
                          <NavLink to={`/profile/${userId}`}>
                              <img 
                                  src={photos.small != null ? photos.small : userPhoto}
                                  alt='alt'
                                  className={s.userPhoto}
                              />
                          </NavLink>

                      </div>
                      <div className=''>
                          {followed ?
                          <button
                              disabled={followingInProgress.some(id => id === userId)}
                              className='btn btn-warning'
                              onClick = {() => unfollow(userId)}
                          >Unfollow
                          </button>:
                          
                          <button
                          disabled={followingInProgress.some(id => id === userId )}
                              onClick = {() => follow(userId)}
                              className='btn btn-success'
                          >Follow
                          </button>}
                      </div>
                  </div>

                  <div className='col-sm-9'>
                      <div>
                          <div>
                              {/* <h4>{user.fullName}</h4> */}
                              <h4>{name}</h4>
                          </div>
                          <div>{status}</div>
                      </div>
                      <hr/>
                      <div className={s.description}>
                          <div>{"user.location.country"}</div>
                          <div>{"user.location.city"}</div>
                      </div>
                  </div>
              </div>
          </div>
      </div>  
    )
}


export {User}