import lira from '../assets/audio/Lyra.mp3'
import psycho from '../assets/audio/Psycho.mp3'

const initialState = {
    audioFiles: [
        {id: 1, name: 'Lyra', src: lira},
        {id: 2, name: 'Psycho', src: psycho}
    ]
}

const musicreducer = (state = initialState, action) => {    
    return state;
}

