import React from 'react'
import { connect } from 'react-redux'
import {Users} from './Users'
import {Preloader} from '../common/preloader/preloader'
import {setCurrentPage, toggleFollowingInProgress, getUsers, follow, unfollow} from '../../redux/users-reducer'
import { compose } from 'redux'
import {getUsersSel, getPageSizeSel, gettoTalUsersCountSel, getCurrentPageSel, getIsFetchingSel, getFollowingInProgressSel, getCurrentPageBlockSel} from '../../redux/users-selector'

class UsersContainer extends React.Component {

    componentDidMount() {

        const {getUsers, currentPage, pageSize} = this.props

        getUsers(currentPage, pageSize)
    }

    componentWillUnmount() {
    }
    
    onPageChanged = (pageNumber) => {
        const {getUsers,pageSize, setCurrentPage} = this.props
        getUsers(pageNumber, pageSize)
        setCurrentPage(pageNumber)
    }



    render() {
        const {users, followingInProgress,
              totalUsersCount, pageSize, isFetching,
              currentPage, unfollow, follow } = this.props
              
        const {onPageChanged} = this     

        return <>
                    {isFetching ?
                    <Preloader /> :
                    <Users 
                        totalUsersCount = {totalUsersCount}
                        pageSize = {pageSize}
                        currentPage = {currentPage}
                        onPageChanged = {onPageChanged}
                        unfollow = {unfollow}
                        follow = {follow}
                        users = {users}
                        followingInProgress = {followingInProgress}
                    />}

                </>
    }

}


const mapStateToProps = (state) => {

    return {
        users: getUsersSel(state),
        pageSize: getPageSizeSel(state),
        totalUsersCount: gettoTalUsersCountSel(state),
        currentPage: getCurrentPageSel(state),
        isFetching: getIsFetchingSel(state),
        followingInProgress: getFollowingInProgressSel(state),
        currentPageBlock: getCurrentPageBlockSel(state)
    }
}


export default compose(
    connect(mapStateToProps, {follow, unfollow, setCurrentPage, toggleFollowingInProgress, getUsers}),
   // withAuthRedirect
)(UsersContainer)

