import React from 'react';
// import {Paginator} from '../common/paginator/paginator'
import {Paginator} from '../common/paginator/paginator'
import {User} from './user'


const Users = (props) => {
    const {users, pageSize,
      //  currentPageBlock, nextPageBlock, prevPageBlock,
        totalUsersCount, currentPage,
         onPageChanged, unfollow, follow, followingInProgress} = props
    
    return (
      <>
        <Paginator
          //currentPageBlock={currentPageBlock}
          currentPage={currentPage}
          //nextPageBlock={nextPageBlock}
          //prevPageBlock={prevPageBlock}
          onPageChanged={onPageChanged}
          totalUsersCount={totalUsersCount}
          pageSize={pageSize}
          
        />         
          <hr/>
          {
            users.map((user, index) => {
                return (
                  <User
                    key={user.id} 
                    follow={follow}
                    unfollow={unfollow}
                    followingInProgress={followingInProgress}
                    user={user}
                  />
                )
            })
          }      
      
      </>
    )
}


export {Users}