import s from './musicItem.module.css'

const MusicItem = (props) => {
    const {name, src} = props
    return (
        <>
            <div className={s.musicItem}>
                <span>{name}</span>
                <audio controls>
                    <source src={src} type="audio/mp3"/>
                    <p>Your browser doesn't support HTML5 audio. Here is a </p>
                    <a href={src}>Скачать</a>                
                </audio>
            </div>
        </>
    )
}

export {MusicItem}