import React from 'react'
import { Field, reduxForm } from 'redux-form'
import s from './login.module.css'
import sf from '../../components/common/formsControls/formsControls.module.css'
import {getLogin} from '../../redux/auth-reducer'
import { connect } from 'react-redux';
import {TextInput} from '../common/formsControls/formsControls'
import {validators} from '../../utils/validators/validators'
import { Redirect } from 'react-router-dom';

const {required, maxLenghtCreator} = validators
const maxLenght20 = maxLenghtCreator(20)

const LoginForm = (props) => {
    const {handleSubmit, error} = props
    
    return (
        <form onSubmit={handleSubmit} className={`${s.loginForm}`}>
            <div>
                <Field
                    name={'login'}
                    component={TextInput}
                    placeholder={`login`}
                    autoComplete={'login'}
                    validate={[
                        required,
                        maxLenght20
                    ]}
                />
            </div>
            <div>
                <Field
                    name={'password'}
                    component={TextInput}
                    type={'password'}
                    autoComplete={'current-password'}
                    placeholder={`password`}
                    validate={[
                        required,
                        maxLenght20
                    ]}
                />
            </div>
            <div>
                <Field
                    name={'rememberMe'}
                    component={'input'}
                    type={'checkbox'} 
                /> remember me
            </div>
                {error &&<div className={`${sf.formSummaryError}`}>
                    {error}
            </div>}
            <div>
                <button  
                    type={'submit'}
                >Login
                </button>
            </div>
        </form>
    )
}

const mapStateToProps = (state) => {
    const {isAuth} = state.auth
    return {
        isAuth
    }
}

const Login = (props) => {
    const {getLogin} = props
    const onSubmit = (formData) => {
        const {login, password, rememberMe} = formData
        getLogin(login, password, rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={'/Profile'}/>
    }
    
    return (
        <>
            <div className={s.login}>
                <h1>Login</h1>
                <LoginReduxForm onSubmit={onSubmit}/>
            </div>
            
        </>
    )
}

const LoginReduxForm = reduxForm({
    form: 'loginForm',
})(LoginForm)

export default connect(mapStateToProps, {getLogin})(Login);