import {MusicItem} from './musicItem/musicItem'
const MusicList = (props) => {
    const {audioFiles} = props
    return (
        <>
            {
                audioFiles.map((audioFile, index) => {
                    const {id, name, src} = audioFile
                    return (
                        <MusicItem
                            key={id}
                            name={name}
                            src={src}
                        />
                    )
                })
            }
        </>
    )
}

export {MusicList}

