import React, {useState} from 'react';
import s from'./paginator.module.css';


const Paginator = (props) => {
  const {pageSize, portionSize = 15,
    //  currentPageBlock, nextPageBlock, prevPageBlock,
      totalUsersCount, currentPage, onPageChanged} = props

  const pagesCount = Math.ceil(totalUsersCount / pageSize)
  const pages = [] 
  for(let i=1; i <= pagesCount; i++) {
    pages.push(i);
  }

  const portionCount = Math.ceil(pagesCount / portionSize) // кол-во порций
  const [portionNumber, setPortionNumber] = useState(1) // номер текущей порции
  const leftPortionNumber = (portionNumber - 1) * portionSize +1
  const rightPortionNumber = portionNumber * portionSize

  // const isButtonDisable = (portionNumber < 1 || portionNumber > portionCount)

  // console.log(isButtonDisable)
  
  return (
        <>
          <button
           className={portionNumber <= 1 ? s.disable : ''}
           disabled={portionNumber <= 1}
           onClick={() => setPortionNumber(portionNumber-1)}
           >prev
          </button>
          <div className={s.pagesWrapper}>
            {   
                pages.filter(page => page >= leftPortionNumber && page <= rightPortionNumber)
                    .map((page, index) => {
                      // console.log(page)
                        return (  
                          <span
                              onClick={() => onPageChanged(page)}
                              className={ `${currentPage === page ? s.selectedPage : ''} ${s.pageNumber}`}
                              key = {page}
                          >{page}
                          </span>
                        )
                    })
                
            }
          </div> 
          <button
            className={portionNumber >= portionCount ? s.disable : ''}
            disabled={portionNumber >= portionCount}
            onClick={() => setPortionNumber(portionNumber+1)}>next</button>
        </>  
        
  )
}


export {Paginator}