import React from 'react';
import s from './header.module.css'
import userPhoto from '../../assets/img/user.png'
import { NavLink } from 'react-router-dom';
import { Redirect } from 'react-router-dom';


const Header = (props) => {
    if (!props.isAuth) return <Redirect to={'/login'}/>
    return (       
        <header className={`${s.header}`}>
            <img src={( props.userData && props.userData.photos.small) ? props.userData.photos.small : userPhoto} alt='logo'/>
            <span>{props.userData && props.userData.aboutMe ? props.userData.aboutMe : 'status'}</span>

            <div className={s.loginBlock}>
                {props.isAuth ?
                <div>
                    <span>{props.login}</span>
                    <button onClick={props.getLogout}>logout</button>
                </div> : 
                <NavLink to={'/login'}>Login</NavLink>} 
            </div>
        </header>
    )
}



export {Header}