
export const validators =  {
    
    required (value) {
        if (value) return undefined
        return 'Field is required'   
    },
    maxLenghtCreator (maxlength) {
        return (value) => {
            if (value && value.length > maxlength) return `Max lenght is ${maxlength} symbols`
            return undefined        
        }
    }
}

