// const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT'
const UPDATE_SELECTED_DIALOG = 'UPDATE-SELECTED-DIALOG'
const SEND_MESSAGE = 'SEND-MESSAGE'


const initialState = {
    selectedDialog: null,
    // newMessageText: '',
    messages: [
        {id:1, message: 'hi', date: '16-10-2020'},
        {id:2, message: 'Hello', date: '16-10-2020'},
        {id:3, message: 'Fuck you', date: '16-10-2020'},
        {id:4, message: 'How are you', date: '16-10-2020'},
        {id:5, message: 'Aloha', date: '16-10-2020'},
        {id:6, message: 'Whats up', date: '16-10-2020'},
    ]        
};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case UPDATE_SELECTED_DIALOG: {
            return {
                ...state,
                selectedDialog: action.newSelectedDialog
            }
        }

        case SEND_MESSAGE: {
            const newMessage = {
                id: Math.floor(Math.random() * (10000 - 4 + 1)) + 4,
                message: action.newMessageBody,
                date: '16-10-2020'
            }
            return {
                ...state,
                messages: [...state.messages, newMessage]
            }
        }

        default:
            return state;
    }
}   

export const sendMessageCreator = (newMessageBody) => ({
    type: SEND_MESSAGE,
    newMessageBody: newMessageBody,   
})


export const updateSelectedDialogCreator = (dialogName) => ({
    type: UPDATE_SELECTED_DIALOG,
    newSelectedDialog: dialogName   
})




export default dialogsReducer;