/* eslint-disable no-fallthrough */
import {usersAPI} from '../api/api'
import {updateObjectInArray} from '../utils/validators/object-helpers'

const initialState = {
    users: [],
    pageSize: 100,
    currentPageBlock: 0,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW: 
            return {...state,
                       users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
            }
        case UNFOLLOW: 
          return {...state,
                    users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
        } 
        case SET_USERS: 
            return {...state,
                    users: [...action.users]
                }        

        case SET_CURRENT_PAGE:   
            return {...state,
                    currentPage: action.page
                }    

        case SET_TOTAL_USER_COUNT: 
            return {...state,
                    totalUsersCount: action.totalCount
                }      
        case TOGGLE_IS_FETCHING: 
            return {...state,
                    isFetching: action.isFetching
                }      
        case TOGGLE_IS_FOLLOWING_PROGRESS: 
            return {...state,
                    followingInProgress: action.followingInProgress
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
                }        
      
        default: 
        return state
    }
}

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET_USERS'
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_TOTAL_USER_COUNT = 'SET_TOTAL_USER_COUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS'

export const followSuccess = (userId) => {
    return {type: FOLLOW, userId}
}


export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId})
export const setUsers = (users) => ({type: SET_USERS, users})
export const setCurrentPage = (page) => ({type: SET_CURRENT_PAGE, page})
export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USER_COUNT, totalCount})
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})
export const toggleFollowingInProgress = (followingInProgress, userId) => ({type: TOGGLE_IS_FOLLOWING_PROGRESS, followingInProgress, userId})



export const getUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleIsFetching(true))
        const response = await usersAPI.getUsers(currentPage, pageSize)
        dispatch(toggleIsFetching(false))
        dispatch(setUsers(response.items))
        dispatch(setTotalUsersCount(response.totalCount))  
    }
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingInProgress(true, userId))
    const response = await apiMethod(userId)
    if (response.resultCode === 0 ) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingInProgress(false, userId))   
}

export const follow =(userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(usersAPI), followSuccess)
    }
}

export const unfollow =(userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId, usersAPI.unFollow.bind(usersAPI), unfollowSuccess)
    }
}


export default usersReducer