import React from 'react';
import s from './nav.module.css'
import {NavLink} from 'react-router-dom'

const Nav = () => {
    return (
        <nav className={`${s.nav}`}>
            <ul className={`navbar-nav mr-auto sidenav`}>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/profile'>
                        <span className={`NavLink-text`}>Profile</span>
                    </NavLink>
                </li>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/users'>
                        <span className={`NavLink-text`}>Users</span>
                    </NavLink>
                </li>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/dialogs'>
                        <span className={`NavLink-text`}>Messages</span>
                    </NavLink>
                </li>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/News'>
                        <span className={`NavLink-text`}>News</span>
                    </NavLink>
                </li>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/Music'>
                        <span className={`NavLink-text`}>Music</span>
                    </NavLink>
                </li>
                <li className={`nav-item ${s.item}`}>
                    <NavLink activeClassName={s.active} to='/Settings'>
                        <span className={`NavLink-text`}>Settings</span>
                    </NavLink>
                </li>

            </ul>
        </nav>
    )
}

export {Nav}