import React from 'react'
import preloader from '../../../assets/img/loading.svg'
import s from'./preloader.module.css';

const Preloader = (props) => {

    return (
        <div style={ {} }>
            <img
                alt='preloader'
                src={preloader}
                className={`${s.preloader}`}
            />
        </div>
    )
}

export {Preloader}